"use client";
import { useEffect, useState, useRef } from "react";
import mqtt from "mqtt";

export function useMqttClient({
    brokerUrl = process.env.NEXT_PUBLIC_MQTT_URL || "wss://broker.emqx.io:8084/mqtt",
    subscribeTopics = [],
    publishTopic = "",
    mqttOptions = {},
}) {
    const [isConnected, setIsConnected] = useState(false);
    const [messages, setMessages] = useState([]);
    const clientRef = useRef(null);

    useEffect(() => {
        const clientId = `nextjs-client-${Math.random().toString(16).slice(2, 10)}`;
        const client = mqtt.connect(brokerUrl, {
            clientId,
            clean: true,
            connectTimeout: 4000,
            reconnectPeriod: 1000,
            ...mqttOptions,
        });

        clientRef.current = client;

        client.on("connect", () => {
            console.log(`å·²é€£æ¥ MQTT Broker: ${brokerUrl}`);
            setIsConnected(true);

            subscribeTopics.forEach((topic) => {
                client.subscribe(topic, (err) => {
                    if (err) {
                        console.error(`è¨‚é–±ä¸»é¡Œ ${topic} å¤±æ•—:`, err);
                    } else {
                        console.log(`å·²è¨‚é–±ä¸»é¡Œ: ${topic}`);
                    }
                });
            });
        });

        client.on("message", (topic, payload) => {
            const message = {
                topic,
                payload: payload.toString(),
                timestamp: new Date().toISOString(),
            };
            setMessages((prevMessages) => [...prevMessages, message]);
        });

        client.on("error", (err) => {
            console.error("MQTT Client éŒ¯èª¤:", err);
            setIsConnected(false);
        });

        client.on("close", () => {
            console.warn("MQTT Client é€£æ¥å·²é—œé–‰");
            setIsConnected(false);
        });

        return () => {
            subscribeTopics.forEach((topic) => client.unsubscribe(topic));
            client.end();
        };
    }, [brokerUrl, subscribeTopics.join(",")]);

    const publishMessage = (topic, msg) => {
        const client = clientRef.current;
        if (!topic) topic = publishTopic;
        if (client && isConnected && topic && msg.trim()) {
            client.publish(topic, msg, { qos: 0 }, (err) => {
                if (err) {
                    console.error(`ç™¼ä½ˆæ¶ˆæ¯åˆ° ${topic} å¤±æ•—:`, err);
                } else {
                    console.log(`å·²ç™¼ä½ˆæ¶ˆæ¯åˆ° ${topic}`);
                }
            });
        }
    };

    return { isConnected, messages, publishMessage };
}
